
.sidenav
{
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 9;
    top: 0;
    right: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
}


#main 
{
    transition: margin-left .5s;
  
}



.icon-close
{
    display: none;
    
}

.icon-menu
{
    cursor: pointer;
}
.mobile-menu
{
  background: rgba(0,0,0,0.9);
  right: -100%;  /* start off behind the scenes */
  height: 100%;
  position: fixed;
  width: 100%;
  z-index: 99;
  top:0;
  display: block;
  padding-top: 50px;
}